.Card{
	display: flex;
	justify-content: center;
	align-items: center;
	width: 20%;
	height: 450px;
	flex-direction: column;
	padding: 20px 20px 20px 20px;
	/* border: #FFBE41 solid 2px; */
	border-radius: 13px;
	margin: 10px 10px 10px 10px;
	/* box-shadow: 0 3px #FFBE41 */
}
.card_top{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
}
.Card_h{
	color: #550312;
	font-family: 'Poppins';
	font-weight: 500;
	text-align: center;
}
.Card_info{
	display: flex;
	font-family: 'Poppins';
	font-weight: 400;
	font-size: 15px;
	line-height: 25px;
	color: #333333;
}
.card_bottom{
	margin-top: 10px;
}

